import React, {useEffect, useState} from 'react'
import { ReactComponent as Spinner } from '../icon/Magnify.svg';
import { form, movielist, listSearch } from './MovieList.module.css';

const MovieList = () => {

  const [movies, setMovies] = useState();
  const [searchInput, setSearchInput] = useState('');
  const [spinner, setSpinner] = useState(false);

  const handlerSubmit = (e) => {
    e.preventDefault();
    setSpinner(true);
    setSearchInput('');

    fetch(`https://yts.mx/api/v2/list_movies.json?query_term=${searchInput}`)
    .then(function(response) {
      return response.json();
    })
    .then(function(myJson) {
  
      setMovies(myJson.data.movies);
  
    })
    .finally(function(){
        setSpinner(false);

      }
    );
  };

  const handlerSearchInput = (e) => {
    setSearchInput(e.target.value);
  };


  // useEffect( () => {
  //   fetch(`https://yts.mx/api/v2/list_movies.json?query_term=${searchInput}`)
  //   .then(function(response) {
  //     return response.json();
  //   })
  //   .then(function(myJson) {
  //     // console.log(JSON.stringify(myJson.data.movies[0].title));
      
  //     // JSON.stringify(myJson.data.movies.map(movie => {
  //     //   console.log(movie.title);
  //     // }));
  
  //     setMovies(myJson.data.movies);
  
  //   });
  // } ,[searchInput]);

  return (
    <>
    <h1>Movie Search</h1>
    <form action="" className={form} onSubmit={handlerSubmit}>
      <input className={listSearch} onChange={handlerSearchInput} value={searchInput}></input>
      <button>검색</button>
    </form>
    {spinner === false ? <ul className={movielist}>
      {
        movies && movies.map((movie) => {
          return (
            <>
            <img src={movie.medium_cover_image}></img>
            <li>{movie.title}</li>
            </>
          )
        })
      }
      </ul> : <Spinner/> }
      
    </>
  )
}

export default MovieList;